*{
    font-size: 62.7%;
    @media screen and (max-width:1024px) {
        font-size: 55%;
    }
    @media screen and (max-width:768px) {
        font-size: 47%;
    }
}

.App{
    min-height: 100vh;
    .screens{
        min-height: 90vh;
    }
}
a {
    text-decoration: none;
    :hover {
    text-decoration:none; 
    cursor:pointer;  
}}


.active{
    background-color: $primarycolor;
    font-weight: 500;
    padding: 1rem 2rem;
}
.two_grid_layout{
    display: flex;

}
.header{
    position: sticky;
    top: 0;
    z-index: 999;
}

.reacticons{
    font-size: 5rem;
}

.loadText{
    height:4rem;
    width:100%;
    background-image: linear-gradient(
    to right,
    #f6f7f8 0%,
    #edeef1 10%,
    #f6f7f8 20%,
    #f6f7f8 100%
  );
  background-size: 200% 100%;
  animation: bgPos 1s linear infinite;
  @keyframes bgPos {
  0% {
    background-position: 50% 0;
  }

  100% {
    background-position: -150% 0;
  }
}
}